@import url("https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@700&display=swap");

.App {
  min-height: 100vh;
  min-width: 98vw;
  background: #333
    url(https://static.tumblr.com/maopbtg/a5emgtoju/inflicted.png) repeat;
  color: white;
  font-size: 20px;
  font-family: monospace;
  text-align: center;
}

.setup {
  padding: 2em;
}
.print-area {
  /* transform: scale(0.5); */
  display: inline-block;
}
.page {
  width: 210mm;
  height: 297mm;
  text-align: center;
  background: white;
  color: black;
  margin-bottom: 1cm;
}
.card {
  width: 6.3cm;
  height: 8.3cm;
  display: inline-block;
  border: 1px solid #b9b9b9;
  border-radius: 9px;
}
.line {
  width: 5.7cm;
  margin: 0.2cm auto;
  display: table;
}
.number {
  display: table-cell;
  vertical-align: middle;
  padding-right: 0.16cm;
  font-size: 0.6cm;
  font-weight: 700;
  font-family: "Open Sans Condensed", sans-serif;
  text-shadow: 1px 1px 0px #7d7d7d;
}
.text-box {
  position: relative;
  width: 5cm;
  height: 1.3cm;
  border: solid 2px;
  display: inline-block;
  vertical-align: middle;
  display: table-cell;
  text-align: center;
  font-size: 0.6cm;
  font-weight: 700;
  font-family: "Open Sans Condensed", sans-serif;
  text-transform: capitalize;
}
.blue {
  color: #329ae4;
}
.blue-border {
  border-color: #329ae4;
}
.purple {
  color: #4f0280;
}
.purple-border {
  border-color: #4f0280;
}
.red {
  color: #ca2500;
}
.red-border {
  border-color: #ca2500;
}
.green {
  color: #10c76a;
}
.green-border {
  border-color: #10c76a;
}
.yellow {
  color: #ff8831;
}
.yellow-border {
  border-color: #ff8831;
}
.blue-border::before,
.purple-border::before,
.red-border::before,
.green-border::before,
.yellow-border::before {
  content: "";
  border: 2px solid #65656578;
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0) scale(1.015) rotate(0.5deg);
  border-radius: 1% 1% 2% 4% / 2% 6% 5% 4%;
}
.blue-border::before {
  transform: translate3d(-50%, -50%, 0) scale(1.015) rotate(0.7deg);
  border: 2px solid #7ba1bd82;
}
.purple-border::before {
  transform: translate3d(-50%, -50%, 0) scale(1.01) rotate(-0.9deg);
  border: 2px solid #8f64ab80;
  border-left: 4px solid #8f64ab80;
  border-radius: 0;
}
.red-border::before {
  transform: translate3d(-50%, -50%, 0) scale(0.97) rotate(-0.5deg);
  border: 5px solid #a7616136;
}
.green-border::before {
  transform: translate3d(-50%, -50%, 0) scale(1.015) rotate(-0.3deg);
  border: 1px solid #0f8c4f;
}
.yellow-border::before {
  transform: translate3d(-50%, -50%, 0) scale(1.015) rotate(-1deg);
  border: 3px solid #d69b578a;
  border-radius: 0;
}
.hidden {
  display: none;
}

/* BUTTON */
a {
  position: relative;
  width: 100%;
  padding-top: 25.92%;
  display: block;
  background-size: 100% 100%;
  font-family: "Open Sans Condensed", sans-serif;
  font-size: 28px;
  margin-bottom: 20px;
  text-decoration: none;
  color: #333;
}
a.background-button {
  background-image: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/323909/yellow-shadow.png);
}
a.background-button:after {
  content: attr(title);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  background-image: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/323909/yellow-top.png);
  transition: all 0.3s;
  display: flex;
  justify-content: center;
  align-items: center;
}
a.background-button:hover:after {
  -webkit-transform: translate(2.5%, 10%);
  transform: translate(2.5%, 10%);
  text-shadow: 2px 2px 1px white;
  color: black;
}
.button-wrapper {
  width: 355px;
  display: inline-block;
  margin: 20px;
}
.button-wrapper a {
  -webkit-filter: hue-rotate(36deg);
  filter: hue-rotate(36deg);
}
/* END BUTTON */

/* TEXT AREA */
#margin {
  margin-left: 12px;
  margin-bottom: 20px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}
#text {
  width: 500px;
  overflow: hidden;
  background-color: #fff;
  color: #222;
  font-family: Courier, monospace;
  font-weight: normal;
  font-size: 24px;
  resize: none;
  line-height: 40px;
  padding-left: 100px;
  padding-right: 100px;
  padding-top: 45px;
  padding-bottom: 34px;
  background-image: url(https://static.tumblr.com/maopbtg/E9Bmgtoht/lines.png),
    url(https://static.tumblr.com/maopbtg/nBUmgtogx/paper.png);
  background-repeat: repeat-y, repeat;
  -webkit-border-radius: 12px;
  border-radius: 12px;
  -webkit-box-shadow: 0px 2px 14px #000;
  box-shadow: 0px 2px 14px #000;
  border-top: 1px solid #fff;
  border-bottom: 1px solid #fff;
}
#title {
  background-color: transparent;
  border-bottom: 3px solid #fff;
  color: #fff;
  font-size: 20px;
  font-family: Courier, monospace;
  height: 28px;
  font-weight: bold;
  width: 220px;
}
#button {
  cursor: pointer;
  margin-top: 20px;
  float: right;
  height: 40px;
  padding-left: 24px;
  padding-right: 24px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 20px;
  color: #fff;
  text-shadow: 0px -1px 0px #000000;
  -webkit-border-radius: 8px;
  border-radius: 8px;
  border-top: 1px solid #fff;
  -webkit-box-shadow: 0px 2px 14px #000;
  box-shadow: 0px 2px 14px #000;
  background-color: #62add6;
  background-image: url(https://static.tumblr.com/maopbtg/ZHLmgtok7/button.png);
  background-repeat: repeat-x;
}
#button:active {
  zoom: 1;
  filter: alpha(opacity=80);
  opacity: 0.8;
}
#button:focus {
  zoom: 1;
  filter: alpha(opacity=80);
  opacity: 0.8;
}
#wrapper {
  width: 700px;
  height: auto;
  margin-left: auto;
  margin-right: auto;
  margin-top: 24px;
  margin-bottom: 100px;
}
/* FIN TEXT AREA */
